import React from "react";
import ReactDOM from "react-dom";
import { BrowserRouter, Route, HashRouter,Switch,Link } from "react-router-dom";
import "./scss/custom.scss";
import Home from "./Home";
import ProjectPage from "./page/ProjectPage";
import AboutPage from "./page/AboutPage";
import Footer from "./components/Footer";
import Navbar from "./components/Navbar";
import GetInTouch from "./components/home/GetInTouch.jsx";
import {navBar,  getInTouch,} from "./editable-stuff/config.js";
import * as serviceWorker from "./serviceWorker";




const App = React.forwardRef((props, ref) => {
  const titleRef = React.useRef();

  return (
    <BrowserRouter basename={process.env.PUBLIC_URL + "/"}>

              {navBar.show && <Navbar ref={titleRef} />}
                <Route path="/" exact component={() => <Home ref={titleRef} />} />
                <Route path="/about" component={() => <AboutPage ref={titleRef} />} />
                <Route path="/ProjectPage" component={() => <ProjectPage ref={titleRef} />} />
        <Switch>
        
        </Switch>
              <Footer>
                {getInTouch.show && (
                  <GetInTouch
                    heading={getInTouch.heading}
                    message={getInTouch.message}
                    email={getInTouch.email}
                  />
                )}
              </Footer>
    </BrowserRouter>

        

  );
})



ReactDOM.render(<App />, document.getElementById("root"));

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();

        
ReactDOM.render(    
    <App/>,

    document.getElementById('root')
);