import React from "react";
import Typist from "react-typist";
import Container from "react-bootstrap/Container";
import Jumbotron from "react-bootstrap/Jumbotron";
import { BsArrowRightCircle } from "react-icons/bs";
const MainBody = React.forwardRef(
  ({ gradient, title, message, icons }, ref) => {
    return (

      <Jumbotron
        fluid
        id="home"
        style={{
          backgroundImage: `url(${gradient})` ,/*`linear-gradient(136deg,${gradient})`,*/
          backgroundSize: "cover",
          backgroundPosition: "center",
        }}
        className="title bg-transparent text-light d-flex flex-wrap m-0 align-items-center" /*bgstyle align-items-center align-content-center */
      >
        <div id="stars"></div>
        <Container className="text-center">
          <br/><br/><br/><br/><br/><br/><br/><br/><br/><br/>
          <h1 ref={ref} className="display-3">
            {title}
          </h1>
          <Typist className="lead typist" cursor={{ show: false }}>
            {" "}
            {message}
          </Typist>
          <div className="p-5">
            {icons.map((icon, index) => (
              <a
                key={`social-icon-${index}`}
                target="_blank"
                rel="noopener noreferrer"
                href={icon.url}
                aria-label={`My ${icon.image.split("-")[1]}`}
              >
                <i className={`fa ${icon.image} fa-2x icons`} />
              </a>
            ))}
          </div>
          <br/><br/><br/><br/><br/><br/><br/><br/><br/><br/>
          <div className="lead">
          <a
            className="btn btn-outline-light btn-md"
            href="#about"//"#/ProjectPage"
            role="button"
            aria-label="Learn more about me"
          >
            <i class="fa fa-solid fa-chevron-down"></i>
          </a>
          </div>
        </Container>

      </Jumbotron>
    );
  }
);


export default MainBody;

