import React from "react";
import ReactDOM from "react-dom";
import Modal from "react-bootstrap/Modal";
import ModalBody from "react-bootstrap/ModalBody";
import ModalHeader from "react-bootstrap/ModalHeader";
import ModalFooter from "react-bootstrap/ModalFooter";
import ModalTitle from "react-bootstrap/ModalTitle";
import {Col,Row,Card} from "react-bootstrap";
import Carousel from "react-bootstrap/Carousel";

const ProfolioCard = ({data}) => {
    const [isOpen, setIsOpen] = React.useState(false);

    const showModal = () => {
        setIsOpen(true);};

    const hideModal = () => {
        setIsOpen(false);};

    const btnHover = () => {
        document.getElementById(data.label).style.filter="brightness(80%)";};

    const btnOut = () => {
        document.getElementById(data.label).style.filter="brightness(100%)";};


  return (
    <div>
    <a class="btn" onClick={showModal} role="button" onMouseOver={btnHover} onMouseOut={btnOut} >
        <img 
            id= {data.label} 
            class="img-thumbnail"
            src={data.cover} 
            alt=""
            width={data.imageSize.width}
            height={data.imageSize.height}
            />
            <p>{data.title}</p>
    </a>

    <Modal show={isOpen} onHide={hideModal} className="pb-5 text-center" size="lg">    
        <div class="modal-content">
            <div class="modal-body text-center pb-5">
                <div class="container">
                    <div >
                        <div>
                            <p></p>
                            <h2 class="portfolio-modal-title text-secondary mb-0">{data.title}</h2>
                            <p></p>
                            <div className="col-md-7"  className="d-flex justify-content-around">
                                <Carousel >
                                      {data.galary.map((value) => {
                                        return (
                                          <Carousel.Item>
                                            <img
                                              className="img-thumbnail d-flex bg-white mb-3"
                                              src={value.img}
                                              alt=".."
                                              width="500"
                                            />
                                            <Carousel.Caption>
                                              <h3></h3>
                                              <p></p>
                                            </Carousel.Caption>
                                          </Carousel.Item>
                                        );
                                      })}
                                </Carousel>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <Modal.Body className="pb-5 text-center">
            <p class="mb-4">{data.paragraph}</p>
            <a
            className="btn btn-outline-light btn-lg "
            href="#/ProjectPage"
            role="button"
            aria-label="Learn more about me"
          >
            more
          </a>
        </Modal.Body>
        <Modal.Footer class="row justify-content-center">
            <button class="btn btn-light" onClick={hideModal}>X</button>
        </Modal.Footer>
      </Modal>
    </div>
  );
}
 
export default ProfolioCard;