import React, { useState, useEffect, useRef } from "react";
import ProfolioCard from "./ProfolioCard";
import {
    Container,
    Row,
    Jumbotron
} from "react-bootstrap";

const Profolio = ({heading, img, imageSize }) => {
    return (  
        
            <section id="profolio" className="section">
            <Container>
                <Jumbotron fluid className="bg-white">
                    <h2 className="display-5 mb-5 text-center text-info">
                        {heading}
                    </h2>
                    <hr/>
                    <Row className="d-flex justify-content-around">
                        {
                            img.data.map(data => {
                                return <ProfolioCard 
                                  data={data}
                                  />
                            })

                        }

                    </Row>
                </Jumbotron>
            </Container>
        </section>
    );
}
 
export default Profolio;